/* global anime */

/**
 *
 * function to create the jQuery type slideDown effect for an object with unknown height
 */
function slideDown(element, duration = 500) {
	// Ensure the element is not already visible
	element.style.display = "block";
	element.style.height = "0px";
	element.style.overflow = 'hidden';
	element.style.transition = `height ${duration}ms ease-in-out`;

	// Temporarily get the element's natural height
	element.style.height = "auto";
	const height = element.scrollHeight + "px";

	// Reset the height and force browser reflow/repaint
	element.style.height = "0px";
	getComputedStyle(element).height;

	// Start the transition
	element.style.height = height;

	// Clean up after the transition
	element.addEventListener("transitionend", function transitionEnd(event) {
		if (event.propertyName === "height") {
			element.style.height = "auto"; // Allow for dynamic content changes
			element.style.overflow = "visible";
			element.removeEventListener("transitionend", transitionEnd);
		}
	});
}


